.scores-mode-select-layout{
    margin: auto;
    width:100%;
}
.scores-mode-select-button{
    font-size: 20px;
    border-radius: 10px;
    width: 100%;
    margin-top:3%;
}
.replay-game-button {
    border-radius: 5px;
    font-size: 16px;
    border:none;
}

.single-score-badge {
    width: 100%;
    height: 100%;
    font-size: 18px;
    text-align: center;
    word-wrap: break-word;
}
.single-score-card{
    color: black;
    margin-top: 1%;
    margin-bottom: 1%;
    border-radius: 10px;
    background-color: transparent;
    font-size: 18px;
    /* opacity: 0.7; */
    margin-left:auto;
    margin-right: auto;
    width: 97%;
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
}
.single-score-card:hover{
    width: 100%;
}